        @media(max-width: 768px){
            .nav{
                justify-content: space-between;
                /* padding-left: 2rem; */
                height: 7rem;
            }
            .navActive{
                justify-content: space-between;
                /* padding-left: 2rem; */
                height: 4rem;
                background:#F8F8F8;
                padding-top: 0;
            }
            .none{
                display: none;
            }
            .hamburger{
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;
                /* align-items: center; */
                height: 3rem; width: 3.5rem;
                margin-right: 2rem;
                cursor: pointer;
                background:#8648e5;
                border-radius: 5px;
                padding-left: 0.7rem;;
                
            }
            .Line1{
                height: 0.00rem;
                width: 2rem;
                border: 0.5px solid #FFFFFF;
                background: #FFFFFF;
                border-radius: 0.5rem;
                transition: 1.5s;
                margin-top: 0.5rem;
            }
            .Line2{
                height: 0.00rem;
                width: 2rem;
                border: 0.5px solid #FFFFFF;
                background: #FFFFFF;
                border-radius: 0.5rem;
                transition: 1.5s;
            }
            .Line3{
                height: 0.00rem;
                width: 1.4rem;
                border: 0.5px solid #FFFFFF;
                background: #FFFFFF;
                border-radius: 0.5rem;
                transition: 1.5s;
                margin-bottom: 0.5rem;
            }
            .list{
                flex-direction: column;
                 align-items:flex-start; 
                 height: auto;
            }
            .container{
                display: flex;
                right: 0;
                left: 40%;
                top: 0rem;
                /* transition: all 2s ease-out ; */
                /* transition-property: transform, opacity, -webkit-transform; */
                /* transition-duration: 0.4s, 0.3s, 0.4s;
                transition-timing-function: ease, ease, ease;
                transition-delay: 0s, 0s, 0s; */
                flex-direction: column;
                position: absolute;
                width: 100%;
                height: 100vh;
                padding-top: 2rem;
                padding-left: 1rem;
                background: #1E2947f4;
                /* border-radius: 1rem; */
                
            }
            /* .Hbtna{
                right:50%;
                padding-top: 1rem;
            } */
            .Logo{
                margin-left: 2rem;
                width: 11rem;
                padding-top: 1rem;
            }
            .LogoB{
                margin-left: 2rem;
                width: 11rem;
                padding-top: 1rem;
            }
        }
