@media (max-width: 768px){
    .box{
        width: 90%;
        height: 22rem;
    }
    .ib{
        /* flex-direction: column; */
        width: 90%;
        flex-direction: column;
    }
    .dev{
        background-image: none;
        padding: 8rem 0 2rem 0;
    }
    .btn{
        margin-top: 0.5rem;
    }
}